.mobile-apology {
	width: 98vw;
	height: 98vh;
}

.mobile-apology-header {
	font-size: 40px;
	font-weight: bold;
	margin: 20%;
}
.mobile-apology-content {
	font-size: 25px;
	font-weight: bold;
	margin: 20%;
	margin-top: 0px;
}

/* TOPBAR */
.topbar {
	width: 100%;
	position: fixed;
	top: 0px;
}

.topbar-content {
	height: 60px;
	margin-left: 20%;
	margin-right: 20%;
}

.topbar-space {
	display: inline-block;
	width: 15px;
}

.topbar-bottom-bar {
	background-color: #527A9C;
	height: 18px;
	display: block;
}

	/* temporary */
.topbar-logo-box {
	display: inline-block;
	text-align: center;
	vertical-align: middle;
	height: 100%;
	width: 100px;
	font-weight: bold;
	font-size: 25px;
	margin-right: 65.2%;
}
.topbar-tab-box {
	display: inline-block;
	text-align: center;
	vertical-align: middle;
	height: 100%;
	width: 100px;
	font-size: 15px;
}
.topbar-tab-box:hover {
	background-color: #B3CADD;
}

/* BANNER */
.homepage-banner-container {
	background-color: #B3CADD;
	position: relative;
	width: 100vw;
	height: 507px;
}

.homepage-banner-header {
	display: inline-block;
	font-size: 60px;
	font-weight: bold;
	text-align: left;
	color: white;
	position: absolute;
	padding: 16px;
	right: 10%;
	bottom: 120px;
}

.homepage-banner-image {
	display: inline-block;
	width: 1200px;
	position: absolute;
	top: 0px;
	left: 0px;
}

/* INTRO */
.homepage-intro {
	margin: 2.5%;
	padding: 2.5%;
	font-size: 25px;
	font-weight: bold;
	background-color: #B3CADD;
}

.learn-more {
	margin-left: 15%;
	font-size: 30px;
	font-style: italic;
	font-weight: bold;
	margin-bottom: 10vh;
}

.homepage-diaspora-section {
	background-color: #B3CADD;
	padding-top: 20px;
	padding-bottom: 20px;
}

.DiasporaChartContainer {
	background-color: white; 
}